import './App.css';

import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types'

import AppBar from '@material-ui/core/AppBar'
import Button from '@material-ui/core/Button'
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import { withStyles, makeStyles } from '@material-ui/core/styles'

import request from 'superagent'

const useStyles = makeStyles((theme) => ({
  tabs: {    
    marginLeft: 'auto',
  },
  title: {
    marginRight: 'auto',
  },
  table: {
    minWidth: 700,    
  },
}));


const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.primary,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);


function TabPanel(props) {
  const { children, value, index } = props;

  return (
    <div role="tabpanel" hidden={value !== index}>
      {value === index && children}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};


const Results = React.memo(function Results(props) {
  const classes = useStyles();
  const { loading, result } = props;

  return(
    <TableBody className={classes.table}>
      {result.map((row, i) => (
          <StyledTableRow key={i}>
            <StyledTableCell>{i+1}</StyledTableCell>
            {[...Array(row.length).keys()].map((col,j) => (
              <StyledTableCell key={i+"-"+j}>{row[col] ? row[col] : ''}</StyledTableCell>
            ))}
          </StyledTableRow>
      ))}
    </TableBody>
  );
})

function Query1() {
  const classes = useStyles();
  const [movieName, setMovieName] = useState("");
  const [userRequest, setUserRequest] = useState({loading: false, result: []});

  const handleChange = (event) => {
    setMovieName(event.target.value);
  };

  const submit = () => {
    setUserRequest({loading: true, result: []});
  };

  const handleKeyDown = (event) => {
    if(event.key === 'Enter'){
      setUserRequest({loading: true, result:[]})
    }
  };

  useEffect(() => {
    if(userRequest.loading) {
      request.get('/api/similar_movies').query({movie_name: movieName})
        .on('error', () => {
          alert('Hubo un error');
        })
        .then(res => {
          setUserRequest({loading: false, result: res.body})
        });
    }
  }, [userRequest]);

  return (
    <div>
      <Paper style={{padding: 16}} square>
        <Typography variant="h5">
          Películas similares
        </Typography>

        <Typography variant="body1">
          Encuentra películas que compartan artistas, que sean de la misma década
          y que compartan géneros.
        </Typography>

         <div style={{marginTop: 30}}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField id="query" value={movieName} label="Nombre de la película" onKeyDown={handleKeyDown} onChange={handleChange}/>
            </Grid>

            <Grid item xs={12}>
              <Button variant="contained" color="primary" size="medium" onClick={submit}>
                Buscar
              </Button>
            </Grid>
          </Grid>
        </div>
       </Paper>

       <TableContainer component={Paper} style={{marginTop: 30}} square>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <StyledTableCell>N°</StyledTableCell>
              <StyledTableCell>Título</StyledTableCell>
              <StyledTableCell>Año de estreno</StyledTableCell>
              <StyledTableCell>Género principal</StyledTableCell>
            </TableRow>
          </TableHead>

          <Results loading={userRequest.loading} result={userRequest.result} />
        </Table>       
      </TableContainer>
    </div>
  );
}

